.Product {
    /* display:inline-block; */
    display: flex;
    flex-direction: column;
    border-radius: 0px 0px 20px 0px;
    /* border-radius: 20px; */
    aspect-ratio: 1 / 1;
    width: 50%;
    max-width: 300px;
    margin: 10px;
    font-size: 16px;
    /* transition: transform 250ms; */
    /* background-color: rgba(211, 211, 211, 0.2); */
    border: 1px solid #d4d4d4;
    /* box-shadow: 0 2px 5px 0 rgba(211, 211, 211, 0.9); */
    transition: flex 250ms ease-in;
}

.ProductContent{
    padding: 25px 25px 0px 25px;
}

.Product img{
    display: block;
    max-height: 175px;
    /* width: 100%; */
    height: auto;
    width: auto;
    margin: auto;
}

